.account-list {
  margin-bottom: 210px;

  @media (max-width: $screen-s) {
    margin-bottom: 40px;
  }

  &__container {

    @media (max-width: $screen-s) {
      display: grid;
      grid-template-columns: 100px 1fr;
      grid-template-areas: "block1 block2";
    }
  }

  &__items {

    @media (max-width: $screen-s) {
      display: flex;
      overflow-x: scroll;
      overflow-y: hidden;
      margin: 0;
    }
    
  }

  &__header {
    width: 100%;
    max-width: 1160px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin: 0 auto 20px auto;
    padding: 0 20px;
    position: relative;

    @media (max-width: $screen-s) {
      // width: 500px;
      // overflow-x: hidden;
      grid-area: block1;
      flex-direction: column;
      height: 460px;
      padding-left: 0;
      margin: 0;
      padding-top: 35px;
    }

    &-name {
      color: $name-clr;
      text-transform: uppercase;
      font-size: 0.75rem;
      font-weight: 500;
      letter-spacing: 1.2px;
      transition: color 0.5s;
      width: 10%;
    }

    &-num,
    &-date,
    &-summ {

      &:hover {
        color: $main-hover-clr;
        cursor: pointer;
        position: relative;

        &:before {
          content: '';
          position: absolute;
          width: 8px;
          height: 5px;
          background-image: url(../img/icons/filtr.svg);
          right: 50px;
          top: 0;
          bottom: 0;
          margin: auto;
          z-index: 50;
        }
      }

    &-num {
      width: 10%;
    }
  }

    &-date {
      width: 15%;

      &:hover {
        &:before {
          left: 50px;
        }
      }
    }

    &-org {
      width: 25%;
    }

    &-reason {
      width: 35%;
    }

    &-summ {
      width: 15%;
      text-align: right;
      position: absolute;
      right: 0;
      padding-right: 20px;
      
      @media (max-width: $screen-s) {
        position: static;
      }

      @media (min-width: $screen-l) {
        position: static;
        z-index: 0;
        padding-right: 0;
      }
    }


  }
}

